* {
  padding: 0;
  border: 0;
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  background-image: url("./img/background/dungeon.png");
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 4vmin);
  color: white;
}

.top-div {
  width: 100%;
}

.enemy-div {
  display: flex;
  justify-content: center;
  align-items: end;
  width: 100%;
  height: 45vh;
}

@keyframes slime-animation {
  0% {
    position: relative;
    bottom: 0vh;
    height: 10vw;
    width: 10vw;
  }
  3% {
    position: relative;
    bottom: 0vh;
    height: 13vw;
    width: 8vw;
  }

  8% {
    position: relative;
    bottom: 5vh;
    height: 10vw;
    width: 10vw;
  }

  15% {
    position: relative;
    bottom: 1vh;
    height: 10vw;
    width: 10vw;
  }
  20% {
    position: relative;
    bottom: 0vh;
    height: 8vw;
    width: 13vw;
  }
  25% {
    position: relative;
    bottom: 0vh;
    height: 10vw;
    width: 10vw;
  }

  100% {
    position: relative;
    bottom: 0vh;
    height: 10vw;
    width: 10vw;
  }
}

.enemy-div img {
  width: 10vw;
  height: 10vh;
  animation-name: slime-animation;
  animation-duration: 4s;
  animation-delay: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.App-link {
  color: #61dafb;
}

.UI {
  display: flex;
}

.UI p {
  margin: 20px 20px;
}

button {
  text-decoration: none;
  border: none;
  font-size: 0.5em;
  margin: 10px;
  padding: 15px;
  border-radius: 10px;
  border: 1px solid #282c34;
}
button:hover {
  background-color: #282c34;
  border: 1px solid white;
  color: white;
}
